<template>
  <div class="tousong-card">
    <div class="tousong-card-top">
      <div class="tousong-card-top-content" ref="content">
        <div v-for="(item, index) in list" :key="index">
          <transition name="slide" mode="in-out">
            <div class="tousong-card-top-item" v-if="item.visible">
              <div
                class="tousong-card-top-img"
                :style="{ 'background-image': `url(${item.src})` }"
              >
                <!-- <img :src="item.src" width="100%" height="100%"/> -->
                <div class="tousong-card-top-description-in-mini hidden-lg">
                  <div class="card-title" v-text="item.title"></div>
                  <div
                    class="card-subtitle"
                    style="padding-top: 6px"
                    v-text="item.address"
                  ></div>
                </div>
                <div class="tousong-card-nav hidden-lg">
                  <div class="tousong-card-nav-arrow-left">
                    <a-icon
                      :class="{ 'disabled-icon': currentIndex == 0 }"
                      type="left"
                      @click="previous"
                    />
                  </div>
                  <div class="tousong-card-nav-arrow-right">
                    <a-icon
                      :class="{
                        'disabled-icon': currentIndex + 1 == list.length,
                      }"
                      type="right"
                      @click="next"
                    />
                  </div>
                </div>
              </div>
              <div
                class="tousong-card-top-description hidden-md hidden-sm hidden-xs"
                style="background: #f7f9ff"
              >
                <div class="card-title" v-text="item.title"></div>
                <div
                  class="card-subtitle"
                  style="padding-top: 15px"
                  v-text="item.address"
                ></div>
                <div class="card-line"></div>
              </div>
            </div>
          </transition>
        </div>
      </div>
      <div class="affix hidden-md hidden-sm hidden-xs">案例</div>
    </div>
    <div class="tousong-card-bottom hidden-md hidden-sm hidden-xs">
      <div class="swiper-box">
        <div class="tousong-card-bottom-left">
          <div
            :class="{ 'card-btn': true, 'card-active': currentIndex == index }"
            style="padding: 0 8px"
            v-for="(item, index) in list"
            :key="index"
            v-text="item.title"
            @click="setIndex(index)"
          ></div>
        </div>
      </div>

      <div class="tousong-card-bottom-right text-center">
        <a-space>
          <a-icon
            :class="{ 'disabled-icon': currentIndex == 0 }"
            style="margin-right: 76px"
            type="left"
            @click="previous"
          />
          <!-- <span></span> -->
          <a-icon
            :class="{ 'disabled-icon': currentIndex + 1 == list.length }"
            type="right"
            @click="next"
          />
        </a-space>
      </div>
    </div>
  </div>
</template>

<script>
import Hammer from "hammerjs";
// 运输投送 切换组件
export default {
  name: "TousongCard",
  data() {
    return {
      currentIndex: 0,
    };
  },
  props: {
    list: {
      type: Array,
      default: function () {
        return [];
      },
    },
  },
  watch: {
    currentIndex(index) {
      this.list.forEach((item, i) => {
        if (i == index) {
          item.visible = true;
        } else {
          item.visible = false;
        }
      });
    },
  },
  methods: {
    setIndex(index) {
      this.currentIndex = index;
    },
    next() {
      let length = this.list.length;
      if (this.currentIndex >= length - 1) {
        return false;
      }
      this.currentIndex++;
    },
    previous() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
  },
  mounted() {
    let hammer = new Hammer(this.$refs.content);
    hammer.get("swipe").set({ direction: Hammer.DIRECTION_HORIZONTAL });
    hammer.on("swipeleft", () => {
      this.next();
    });
    hammer.on("swiperight", () => {
      this.previous();
    });
  },
};
</script>
